html {
    height: 100%;
}

body {
    background: lightgrey;
    height: 100%;
    padding: 0;
    margin: 0;
    font-family: Arial, Helvetica, sans-serif;
    color: black;
}

.wrapper {
    margin: 0 auto;
    padding: 1em;
    /* width: 900px; */
}

h1 {
    color: #063754;
    margin: 1em 0.5em;
}

section {
    background:slategray;
    color: #063754;
    margin: 1em;
    padding: 1em;
}

section .col {
    display: flex;
    flex-direction: row;
}

section .col > div {
    flex-grow: 1;
    flex-basis: 0;
}

section .col > div > button {
    float: right;
}